body{
    background-color :hsl(0, 0%, 92%);
    font-family: 'Lato';
    margin: 0;
    padding: 0;
    height: 100%;
}

.login_view_header_QHD{
    display: fixed;
    top: 0px;
    width: 100%;
    height: 54px;
    background-color: white;
    align-self: start;
}

.login_view_logo_div_QHD{
    width: 110px;
    height: 54px;
    background-color: #0069bb;
}

.login_view_logo_QHD{
    display: inline-block;
    width: 98px;
    height: 42px;
    padding: 6px;
    fill: white;
}

.login_view_logo_div_HD{
    width: 90px;
    height: 38px;
    background-color: #0069bb;
}

.login_view_header_HD{
    display: fixed;
    top: 0px;
    width: 100%;
    height: 38px;
    background-color: white;
    align-self: start;
}

.login_logo_div_HD{
    width: 60px;
    height: 25px;
    background-color: #0069bb;
}

.login_view_logo_HD{
    display: inline-block;
    width: 60px;
    height: 25px;
    padding: 6px 9px;
    fill: white;
}

.login_view_position {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}