.normal_button{
    transition-duration: 0.4s;
    background-color: #0069BB;
    height: 36px;
    color: white;
    border-radius: 18px;
    border-width: 0px;
    text-align: center;
    padding: 8px 0;
    font-size: 15px;
    font-weight: 400;
}

.normal_button:hover{
    background-color: #2089DB;
}

.normal_button:disabled{
    background-color: #BABABA;
}