.check_div{
    display: table;
    height: 32px;
}

.check_label{
    display: table-cell;
    vertical-align: middle;
}

.check_title{
    padding-right: 13px;
}

.check_label > .check_box {
    display: none;
}

.check_label > .check_box + *::before {
    content: "";
    display: inline-block;
    vertical-align: bottom;
    width: 1rem;
    height: 1rem;
    margin-right: 5px;
    border-radius: 2px;
    border: 1px solid #E3E3E3;
    flex-shrink: 0;
}

.check_label > .check_box:checked + * {
    color: #0069bb;
}

.check_label > .check_box:checked + *::before {
    content: "v";
    color: white;
    text-align: center;
    background: #0069bb;
    border-color: #0069bb;
}

.check_label > .check_box:hover + * {
    color: #2089DB;
}

.check_label > .check_box:hover + *::before {
    border: 1px solid #2089DB;
}