table{
    font-size: 18px;
    width: 100%;
    text-align: center;
    border-collapse:collapse;
    table-layout:auto;
}

thead{
    font-weight: bold;
    border: none;
}

thead tr{
    height: 70px;
}

tbody{
    font-weight: 400;
    /* height: 920px; */
    background-color: white;
}

td{
    border: none;
}

.table_body_list_tr{
    line-height: 89px;
    border-bottom: 3px solid #F6F6F6;
    pointer-events: auto;
}

.table_wrapper{
    width: 100%;
    height: 990px;
    position: relative;
}

.tbody_background_div{
    top: 70px;
    position: absolute;
    width: 100%;
    height: 920px;
    background-color: white;
}

.table_div{
    width: 100%;
    position: relative;
}

.table_body_list_tr:hover{
    background-color: #F6F6F6;
}

.table_body_list_tr:hover .custom_label_div{
    background-color: #E3E3E3;
}

.table_body_progam_td{
    display: flex;
    align-items: center;
    align-content: center;
}

.table_progame_td_div{
    height: 91px;
    display: flex;
    align-items: center;
    align-content: center;
    vertical-align: middle;
    margin: 0 auto;
}
